import http from "../http-common";

class DocsDataService {
  getAll() {
    return http.get("/documents");
  }

  get(id) {
    return http.get(`/documents/${id}`);
  }

  create(data) {
    return http.post("/documents", data);
  }

  update(id, data) {
    return http.put(`/documents/${id}`, data);
  }

  delete(id, name) {
    return http.delete(`/documents/${id}/${name}`);
  }

  deleteAll() {
    return http.delete(`/documents`);
  }

  findByHandler(name) {
    return http.get(`/documents?name=${name}`);
  }
}

export default new DocsDataService();
