<template>
  <div>
    <v-tooltip bottom>
      <template v-slot:activator="{ on: tooltip }">
        <v-hover v-slot="{ hover }">
          <v-btn
            block
            height="80"
            class="success mr-1"
            :elevation="hover ? 8 : 2"
            v-on="{ ...tooltip }"
            :disabled="acceptAssignmentDisabled"
            @click="openDialog"
          >
            <div class="d-flex flex-column align-center">
              <v-icon>mdi-account-check</v-icon>
              <span class="action-text">Accept Assignment</span>
            </div>
          </v-btn>
        </v-hover>
      </template>
      <span>ACCEPT ASSIGNMENT</span>
    </v-tooltip>

    <v-dialog persistent v-model="dialog" max-width="500px">
      <v-card class="text-center">
        <v-card-title class="justify-center text-h5 grey lighten-2">
          ACCEPT ASSIGNMENT?
        </v-card-title>
        <v-card-text class="mt-3 pa-0">
          CASEFILE NUMBER: AA
          <span
            v-for="(dept, index) in filterDept(casefile.refType)"
            :key="index"
          >
            /{{ dept.name }}
            <span
              v-for="(subDept, idx) in filterSubDept(casefile.subRefType)"
              :key="'sub-' + idx"
            >
              /{{ subDept.subCode }}
            </span>
          </span>
          /{{ casefile.id }}/{{ yearOnly(casefile.createdAt) }}
          <span
            v-for="branch in filterBranches(casefile.branch)"
            :key="branch.id"
          >
            /{{ branch.brCode }}
          </span>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn tile small dark class="mt-3 mr-2 error" @click="closeDialog">
            Cancel
          </v-btn>
          <v-btn tile small class="mt-3 mr-2 success" @click="acceptAssignment">
            OK
          </v-btn>
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import CasefileDataService from "../../services/CasefileDataService";
import moment from "moment";

export default {
  name: "acceptAssignment",
  props: {
    casefile: { type: Object, required: true },
    refType: Array,
    branches: Array,
    subRefTypes: Array,
    insurer: Array,
    members: Array,
    handlers: Array,
    states: Array,
  },
  data() {
    return {
      dialog: false,
    };
  },
  computed: {
    acceptAssignmentDisabled() {
      return !(
        this.casefile.fileStatus === "ADAC" &&
        this.casefile.adjuster === this.$store.state.auth.user.id
      );
    },
  },
  methods: {
    openDialog() {
      this.dialog = true;
    },
    closeDialog() {
      this.dialog = false;
    },
    acceptAssignment() {
      CasefileDataService.update(
        this.casefile.id,
        this.casefile,
        (this.casefile.fileStatus = "INVE"),
        (this.casefile.dateStartInv = moment().format())
      )
        .then((response) => {
          this.$store.dispatch("setSnackbar", {
            showing: true,
            color: "green",
            text:
              (response && response.data && response.data.message) ||
              toString(),
          });
          this.$emit("accepted");
        })
        .catch((e) => {
          console.log(e);
        });
      this.closeDialog();
    },
    filterDept(item) {
      return this.refType.filter((dept) => dept.id === item);
    },
    filterSubDept(item) {
      return this.subRefTypes.filter((subRef) => subRef.id === item);
    },
    filterBranches(item) {
      return this.branches.filter((branch) => branch.id === item);
    },
    yearOnly(value) {
      return moment(value).format("YY");
    },
  },
};
</script>
<style scoped>
.action-text {
  font-size: 0.9rem;
  white-space: normal;
  text-align: center;
  line-height: 1.2;
}
</style>
